@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
}
.container {
  height: 100vh;
  margin: auto;
  display: flex;
  flex-direction: column;
}
/* ====== HEADER ===== */
.header {
  text-align: center;
  margin-top: 30px;
  margin-bottom: 20px;
}
.header__logo {
  height: 87px;
  width: 100px;
}
.header__title {
  font-family: "Raleway";
  font-size: 60px;
  margin: 10px;
}
.header__subtitle {
  font-family: "Raleway";
  font-size: small;
  color: gray;
}

/* ====== messageList ===== */
.messageList {
  flex: 1 1;
  overflow-y: auto;
}
.message_date {
  text-align: center;
}
::-webkit-scrollbar {
  width: 6px;
  background-color: #f5f5f5;
}
::-webkit-scrollbar-thumb {
  background-color: #000000;
}

/* ====== FOOTER ===== */
.footer {
  margin: 0 10px;
  margin-bottom: 5px;
}
.app__form {
  padding: 10px 20px;

  z-index: 1;
  background-color: #e9e9eb;
  margin: 5px;
  border-radius: 35px !important;
}
.app__formControl {
  display: flex !important;
  flex-direction: row !important;
}
.app__input {
  flex: 1 1;
}
.app__iconButton {
  flex: 0 1;
}

.messages {
  padding: 10px;
  margin: 10px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  max-width: 60%;
}

.messages__user {
  margin-left: auto;
  color: white;
  text-align: left !important;
}

.messages__userCard {
  background-color: #0b81ff !important;
  border-radius: 35px !important;
  color: white!important;
}

.messages__guestCard {
  background-color: #e9e9eb !important;
  border-radius: 35px !important;
}

.username {
  text-transform: capitalize;
  text-align: left;
}

